<template>
  <div>
    <h1>{{ title }}</h1>
    <form @submit.prevent="mostrar">
      <input type="number" v-model.number="paragraphs" />
      <input type="submit" />
    </form>
    <p>{{ dato }}</p>
  </div>
</template>

<script>
import { LoremIpsum } from "lorem-ipsum";
export default {
  props: {
    title: String,
  },
  data: () => ({
    lorem: new LoremIpsum({
      sentencesPerParagraph: {
        max: 8,
        min: 4,
      },
      wordsPerSentence: {
        max: 16,
        min: 4,
      },
    }),
    paragraphs: 0,
    dato: "",
  }),
  methods: {
    mostrar() {
      console.log(this.lorem.generateParagraphs(this.paragraphs));
    },
  },
};
</script>
